.card__header {
    /* offsets */
    margin: 0;
    padding: 0;
    /* font */
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 1.2;
    /* decoration */
    color: var(--black);
    max-width: 190px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
