.header {
    /* size */
    height: 74px;
    max-width: 880px;
    /* offsets */
    margin: 0 auto;
    padding-top: 52px;
    border-bottom: 1px solid var(--gray);
    /* smooth */
    transition: 0.5s ease-in-out;
}

@media screen and (max-width: 1023px) {
    .header {
        /* size */
        height: 55px;
        /* offsets */
        padding-top: 28px;
        padding-left: 28px;
    }
}
